<template>
  <div class="page-jobs">

    <mt-header title="">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="post-info">
      <mix-box 
        :image="data.cp_logo" 
        :title="data.job_name" 
        :salary="''+data.price_msg+''" 
        :source="data.cp_name">
      </mix-box>
      <div class="post-info-foot">
        <span v-for="(item, index) in data.tags" key="index">{{item}}</span>
      </div>
    </div>
    
    <div class="box box-loc" v-show="data.address">
      <common-title iconClass="icon-loc" :text="data.address"> </common-title>
    </div>

    <div class="box">
      <common-title iconClass="icon-money" text=""> </common-title>
      <div class="box-desc">
        {{data.my_money}}
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-info" text=""> </common-title>
      <div class="box-desc">
        {{data.my_need}}
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-welfare-blue" text=""> </common-title>
      <div class="box-desc">
        {{data.my_welfare}}
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-condition" text=""> </common-title>
      <div class="box-desc">
        {{data.rec_condition}}
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text=""> </common-title>
      <div class="box-desc">
        {{data.my_notice}}
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text=""> </common-title>
      <div class="box-desc" v-if="showPic">
        <mt-swipe :auto="4000">
          <mt-swipe-item v-for="(item, i) in data.job_images" key="i">
            <img :src="item" />
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="box-desc" v-else></div>
    </div>

    <div class="fix-bottom fix-post">
      <mt-button @click.native="handleAsk">
        <img src="../../../images/consultation_online_icon@3x.png" height="20" width="20" slot="icon">
        
      </mt-button>
      <mt-button @click.native="handleTell">
        <img src="../../../images/consultation_call_icon@3x.png" height="20" width="20" slot="icon">
        
      </mt-button>
      <mt-button @click.native="handleApply" type="primary">
        
      </mt-button>
    </div>  

</div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import mixBox from '../../common/mixBox.vue'
import Util from '../../common/Util'

export default {
  name: 'page-jobs',

  components: {
      commonTitle,
      mixBox
  },

  data() {
    return {
      id:0,
      showPic: false,
      data: {},
      jobDetailApi: 'http://123.206.221.113:8011/api/recruit/jobdetail'
    };
  },

  created() {
    //ID
    this.id = this.$route.query.id;
    if(!this.id){
        this.$toast('');
        return false;
    }

    this.$indicator.open();

    setTimeout(() => {
        this.getData();

        this.$indicator.close();
    }, 1000);
    
  },

  methods: {
    back () {
      window.history.back();
    },
    
    getData () {
      let query = {
        "id": this.id
      };
      this.$http.get(this.jobDetailApi, {
        params: query
      }).then((res) => {
          let data = res.data;
          if(data.status.code == 0){
            this.data = data.result;
            if(this.data.job_images.length > 0) {
              this.showPic = true;
            }
          }else{
            this.$toast('');
          }
      }, (response) => {
          this.$toast('');
      });
    },

    handleAsk: function() {
      this.$messagebox({
        title: '',
        message: '',
        type:'alert',
        showCancelButton: false,
        confirmButtonText: ''
      }).then(action => {
        console.log(action);
      });
    },

    handleTell: function() {
      location.href = "tel:"+this.data.tel;
    },

    handleApply: function() {
      let isLogin = Util.isLogin();

      if(!isLogin){
        this.$messagebox({
          title: '',
          message: '',
          type:'confirm',
          showCancelButton: true,
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(action => {
          if(action == "confirm"){
            this.goLogin();
          }
        });
      }else{
        this.goApply();
      }
    },

    goLogin (){
      this.$router.push({ path: '/user/login', query: { refer: 'jobs'}})
    },

    goApply () {
      this.$router.push({ path: '/user/login'})
    }
  }
};
</script>

<style lang="css">
  .page-jobs{
    padding-bottom:50px;
  }

  .post-info{
    padding:20px 15px;
    background:#fff;
  }
  .post-info-foot{
    margin-top:15px;
  }
  .post-info-foot span{
    display: inline-block;
    margin-right:12px;
    font-size: 14px;
    padding: 0 12px;
    height: 33px;
    line-height:33px;
    border: 1px solid #D7D7D7;
    border-radius:4px;
    background:#fff;
    color: #828282;
  }

  .box-loc{
    border-bottom:none;
  }

  .mint-swipe {
    width: 100%;
    height: 160px;
    overflow: hidden;
  }
  .mint-swipe-item img{
    width: 100%;
    height: 100%;
  }

  .fix-post{
    display:flex;
  }
  .fix-post .mint-button--normal{
    flex: 1;
    background:#fff;
    font-size:14px;
    box-shadow:none;
    font-size:13px;
  }
  .fix-post .mint-button:first-child{
    border-right:1px solid #E9E9E9;
  }
  .fix-post .mint-button--normal label{
    display:block;
  }
  .fix-post .mint-button--primary{
    flex: 2;
    background: #0977C5;
    box-shadow:none;
    font-size:16px;
  }
</style>



