<template>
  <div class="page-renew">

    <mt-header title="">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="box check-info">
      <div class="check-date" @click.stop="handleDate">
        <div class="check-in"> <span>{{begin}}</span></div>
        <div class="check-day">{{count}}</div>
        <div class="check-out"> <span>{{end}}</span></div>
      </div>
    </div>

    <div class="fix-bottom">
      <mt-button @click.native="handleRenew" size="large" type="primary"></mt-button>
    </div>
    
  </div>
</template>

<script>
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-renew',

  components: {

  },

  data() {
    return {
      begin: '',
      end:'',
      count: 1,
      start_time: '',
      end_time: '',
      oid: '',
      renewApi: ajaxUrl.order_renew
    };
  },

  created() {
    let begin = Util.getToday();
    let end = Util.getTomorrow();

    let data = localStorage.getItem("booking-date");
    if(data && data != ""){
      let date = JSON.parse(data);

      if(Util.isObject(date)){
        begin = date.begin;
        end = date.end;
        this.count = date.count;
      }
    }

    //
    this.begin = begin[1]+""+begin[2]+"",
    this.end = end[1]+""+end[2]+"";

    //
    this.start_time = begin.join("-");
    this.end_time = end.join("-");

    //ID
    this.oid = this.$route.query.id;

  },

  destroyed () {
    localStorage.setItem("booking-date", ""); //
  },

  methods: {
    handleDate () {
      this.$router.push({ path: '/dormitory/date', query: { refer: 'renew' }})
    },

    handleRenew () {
      this.$http.post(this.renewApi, {
        'order_id': this.oid,
        'predetermine_start_time': this.start_time,  
        'predetermine_end_time': this.end_time,  
        'token': Util.getToken()
      }).then((res) => {
          let data = res.data;
          if(data.status.code == 0){
            let result = data.result;

            if(result && result.order.order_id){
              this.$router.push({ path: 'detail', query: { id: result.order.order_id}})
            }
          }else if(data.status.code == 504){
            this.$toast(data.status.msg);
            setTimeout(() => {
              this.goLogin();
            }, 2000);
          }else if(data.status.code == 500){
            this.orderConfirm();
          }
      }, (res) => {
          this.$toast('');
      });
      
    },

    orderConfirm () {
      let msg = '<span>'+this.begin+'<\/span><span>'+this.end+'<\/span>'

      this.$messagebox({
        title: '',
        message: msg,
        type:'confirm',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
      }).then(action => {
        if(action == "confirm"){
          window.history.back();
        }
      });
    },

    goLogin () {
      this.$router.push({ path: '/user/login', query: { refer: 'order-renew'}})
    }

  }
};
</script>



