<template>
  <div class="page-order-place">

    <mt-header title="">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="box">
      <common-title iconClass="icon-house" text=""> </common-title>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label>  </label>
            <span v-if="build.total_amount" class="orange"> {{build.total_amount}} </span>
          </li>
          <li>
            <label>     </label>
            <span v-if="build.sex == 1">  </span>
            <span v-else-if="build.sex == 2">  </span>
          </li>
          <li>
            <label></label>
            <span> {{build.room_name}} </span>
          </li>
          <li>
            <label></label>
            <span> {{build.berth_name}}</span>
          </li>
          <li>
            <label></label>
            <span> {{build.date_range}} </span>
          </li>
          <li>
            <label></label>
            <span> {{build.building_address}} </span>
          </li> 
        </ul>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-user" text=""> </common-title>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label>  </label>
            <span> {{user.predetermine_name}} </span>
          </li>
          <li>
            <label></label>
            <span> {{user.predetermine_phone}} </span>
          </li>
          <li>
            <label></label>
            <span> {{user.predetermine_id_card}} </span>
          </li>
          <li>
            <label></label>
            <span v-if="user.predetermine_type == 1">  </span>
            <span v-else-if="user.predetermine_type == 2">  </span>
            <span v-else>  </span>
          </li>
        </ul>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text=""> </common-title>
      <div class="box-desc">
        <p v-html="order.order_reminder">{{order.order_reminder}}</p>
      </div>
    </div>

    <div class="fix-bottom" v-if="order.order_id">
      <mt-button size="large" type="primary" @click.native="handlePay"></mt-button>
    </div>
  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-order-place',

  components: {
      commonTitle
  },

  data() {
    return {
      build:{},
      user:{},
      order:{},
      bids: '',
      creatOrderApi: ajaxUrl.create_order
    };
  },

  created() {
    this.$indicator.open();

    //ID
    this.bids = this.$route.query.bids;
    setTimeout(() => {
        this.bids != '' && this.getData();

        this.$indicator.close();
    }, 500);
  },

  methods: {
    back () {
      if(!this.order.order_id){
        this.$router.go(-3);  //
      }else{
        this.$router.push({ path: '/order/list', query: { refer: 'place'}})
      }
    },

    getData () {
      this.$http.post(this.creatOrderApi, {
        'berth_id': this.bids.split(','),
        'token': Util.getToken()
      }).then((res) => {
        let data = res.data;
        if(data.status.code == 0){
          let result = data.result;

          if(result){
            this.build = result.building;
            this.user = result.predeterminer;
            this.order = result.order;
          }
        }else if(data.status.code == 504){
          this.$toast(data.status.msg);
          setTimeout(() => {
            this.goLogin();
          }, 2000);
        }else if(data.status.code == 500){
          //
          //this.goBack();
        }
      }, (res) => {
          this.$toast('');
      });
    },

    goBack () {
      this.$messagebox({
        title: '',
        message: '',
        type:'alert',
        showCancelButton: false,
        confirmButtonText: ''
      }).then(action => {
        console.log(action);
      });
    },

    goLogin () {
      this.$router.push({ path: '/user/login', query: { refer: 'place'}})
    },

    handlePay () {
      //todo
      console.log('')
    }
  }

};
</script>

<style lang="css">
  .page-order-place{
    padding-bottom:50px;
  }
</style>


